* {
    margin: 0;
    padding: 0; 
}

$color_white: white;
$color_dark-grey: #333333;
$color_grey: #929292;
$color_light-grey: #dcdcdc;
$color_blue: #0f7bff;
$color_light-blue: #f5fcfd;
$color_dark-blue: #0063dd;
$color_blue-greyish: #dfedff;
$color_purple-greyish: #f5dfff;
$color_green-greyish: #dfffe3;
$color_hover: #0ed085;

.text {
    &__h1 {
        font-family: 'Quicksand', "Open Sans", Arial, sans-serif;
        font-size: 42px;
        font-weight: 700;
        //line-height: 24px;
    }
    &__h1_small {
        font-family: 'Quicksand', "Open Sans", Arial, sans-serif;
        font-size: 36px;
        font-weight: 700;
        //line-height: 24px;
    }

    &__h2 {
        font-family: 'Quicksand', "Open Sans", Arial, sans-serif;
        font-size: 32px;
        font-weight: 500;
        //line-height: 36px;
    }
    &__h2_small {
        font-family: 'Quicksand', "Open Sans", Arial, sans-serif;
        font-size: 24px;
        font-weight: 500;
    }
    &__h3 {
        font-family: 'Quicksand', "Open Sans", Arial, sans-serif;
        font-size: 18px;
        font-weight: 500;
        //line-height: 27px;
    }
    &__body {
        font-family: 'Quicksand', "Open Sans", Arial, sans-serif;
        font-size: 16px;
        //line-height: 27px;
    }
    &__body_small {
        font-family: 'Quicksand', "Open Sans", Arial, sans-serif;
        font-size: 14px;
    }
    &__uppercase {
        text-transform: uppercase;
    }
    &__color_dark-grey {
        color: $color_dark-grey;
    }
    &__color_white {
        color: $color_white;
    }
    &__color_grey {
        color: $color_grey;
    }
    &__color_blue {
        color: $color_blue;
    }
    
}
